<template>
  <div class="transaction-detail-table-4">
    <el-table
      :data="data"
      tooltip-effect="dark">
      <el-table-column
        prop="create_time_full"
        :label="$t('assets.transDetail.thead.table4.text1')">
      </el-table-column>
      <el-table-column
        prop="currency_mark"
        :label="$t('assets.transDetail.thead.table4.text2')">
      </el-table-column>
      <el-table-column
       :label="$t('assets.transDetail.thead.table4.text3')">
        <template slot-scope="scope">
          <!-- 实付数量 -->
          {{ scope.row.num }} {{ scope.row.currency_mark }}
        </template>
      </el-table-column>
      <el-table-column
        :label="$t('assets.transDetail.thead.table4.text4')">
        <template slot-scope="scope">
          <!-- 到账数量 -->
          {{ scope.row.actual }} {{ scope.row.currency_mark }}
        </template>
      </el-table-column>
      <el-table-column
        :label="$t('assets.transDetail.thead.table4.text5')">
        <template slot-scope="scope">
          <!-- 确认数量 -->
          {{ scope.row.confirm_num }}
        </template>
      </el-table-column>
      <el-table-column
        prop="ti_id"
        :show-overflow-tooltip="true"
        :label="$t('assets.transDetail.thead.table4.text6')">
      </el-table-column>
      <el-table-column
        :label="$t('assets.transDetail.thead.table4.text7')">
        <template slot-scope="scope">
          {{ status(scope.row.status) }}
        </template>
      </el-table-column>
      <el-table-column
        :show-overflow-tooltip="true"
        :label="$t('assets.transDetail.thead.table4.text8')">
        <template slot-scope="scope">
          {{ remark(scope.row.remarks) }}
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  export default {
    name: 'transaction-detail-table-4',
    components: {
    },
    props: {
      data: {
        type: Array,
        default() {
          return []
        }
      }
    },
    data() {
      return {
      }
    },
    methods: {
      status(val) {
        if (val <= 0 || val > 3) return this.$t('assets.transDetail.tbody.typeFilter.text6')
        // type值为1,2,3时，显示充值中，成功或已撤销
        const map = [
          this.$t('assets.transDetail.tbody.inStatusFilter.text1'),
          this.$t('assets.transDetail.tbody.inStatusFilter.text2'),
          this.$t('assets.transDetail.tbody.inStatusFilter.text3')
        ]
        return map[val - 1]
      },
      remark(val) {
        // 若没有填写备注，显示“无”
        if (!val) {
          return this.$t('assets.transDetail.tbody.remarkFilter.text1')
        } else {
          return val
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .transaction-detail-table-4{
    
  }
</style>



